import { FC } from 'react';

import { TwoColumnStackedProps } from '../Layout.types';
import OneColumnLayout from '../OneColumn';
import StackedLayout from '../StackedLayout';
import TwoColumnLayout from '../TwoColumn';

const TwoColumnStacked: FC<TwoColumnStackedProps> = ({
  topLeft,
  topRight,
  middle,
  bottomLeft,
  bottomRight,
  topAsideVisibleAtWidth,
  bottomAsideVisibleAtWidth,
  ...rest
}) => (
  <StackedLayout {...rest}>
    <TwoColumnLayout
      main={topLeft}
      aside={topRight}
      asideVisibleAtWidth={topAsideVisibleAtWidth}
    />
    <OneColumnLayout>{middle}</OneColumnLayout>
    <TwoColumnLayout
      main={bottomLeft}
      aside={bottomRight}
      asideVisibleAtWidth={bottomAsideVisibleAtWidth}
    />
  </StackedLayout>
);

export default TwoColumnStacked;
